@import "src/styles/index";

.section {
  padding: 29px 0 19px 0;
}

.title {
  font-size: 31.5px;
  font-weight: 300;
  color: #39373a;
}

.input {
  margin-top: 12px;
  padding-left: 10px;
  max-width: 519px;
  height: 30px;
  width: 100%;
  border: 1px solid #c5c5c5;
  border-radius: 7px;
}

.groupBlocks {
  margin-top: 62px;
  display: flex;
  @include mobile {
    flex-wrap: wrap;
  }
}

.block {
  padding-top: 45px;
  padding-bottom: 28px;
  width: 25%;
  cursor: pointer;
  text-align: center;
  border-left: 1px solid #d8dadc;
  &:first-child {
    border: none;
  }
  &:hover {
    background-color: #edeeef;
    box-shadow: 0 0 9px 0 rgba(0, 0, 0, 0.21);
  }
  @include mobile {
    width: 50%;
    border: none;
    &:nth-child(even) {
      border-left: 1px solid #d8dadc;
    }
    &:nth-child(-n + 2) {
      border-bottom: 1px solid #d8dadc;
    }
  }
}
.wrapperIcon {
  width: 120px;
  height: 120px;
  margin: auto;
  border: 1px solid #5c5a5d;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.text {
  margin-top: 36px;
  font-size: 19.5px;
  font-weight: 300;
  color: #39373a;
  @include mobile {
    font-size: 14px;
  }
}
