@import "src/styles/index";

.footer {
  background-color: #39373a;
  padding: 32px 0 89px 0;
}

.inner {
  display: flex;
  justify-content: space-between;

  @include mobile {
    flex-direction: column;
    row-gap: 20px;
  }
}
.logoSocials {
  width: fit-content;
  margin-right: 15px;
}
.socials {
  margin-top: 9px;
  display: flex;
  justify-content: space-between;
}
.number {
  display: block;
  font-size: 16.5px;
  font-weight: 300;
  line-height: 1.3;

  color: #fff;
}

.icons {
  display: flex;
  align-items: baseline;
  column-gap: 19px;
}
.navWrapper {
  display: flex;
  column-gap: 47px;
}
.link {
  font-size: 16.5px;
  font-weight: 300;
  line-height: 1.3;
  color: #fff;
  &:hover {
    color: rgb(81, 201, 161);
  }
}
.contacts {
  max-width: 175px;
  font-size: 16px;
  font-weight: 300;
  line-height: 1.3;
  color: #fff;
  span {
    display: block;
  }
}
