@import "src/styles/index";

.section {
  background-image: url("../../../assets/images/main/face.png");
  background-repeat: no-repeat;
  background-position-x: right;
  background-size: cover;
  width: 100%;
  background-color: rgb(237, 238, 239);
  padding: 79px 0 19px 0;
}

.title {
  font-size: 31.5px;
  font-weight: 300;
  color: #39373a;
}
.text {
  margin-top: 16px;
  max-width: 700px;
  font-size: 21px;
  font-weight: 300;
  color: #39373a;
}

.button {
  margin-top: 13px;
  background-color: #5c5a5d;
  font-size: 18px;
  font-weight: 300;
  color: #fff;
  padding: 8px 21px 8px 22px;
  border-radius: 13.2px;
  cursor: pointer;
  &:hover {
    color: rgb(81, 201, 161);
  }
}
