@import "src/styles/index";

.section {
  position: relative;
  background-image: url("../../../assets/images/main/mainImg.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  // max-height: 549px;
  height: 549px;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  padding-bottom: 46px;
  box-shadow: inset 0px -20px 150px 0px;

  @include mobile {
    height: 420px;
  }
}
.img {
  width: 100%;
  height: 560px;
}
.groupBlocks {
  display: flex;
  @include iPadMini {
    flex-wrap: wrap;
    padding: 0 15px;
    row-gap: 15px;
  }
}

.block {
  width: 25%;
  text-align: center;
  @include iPadMini {
    width: 50%;
  }
}
.wrapperIcon {
  width: 102px;
  height: 102px;
  margin: auto;
  border: 1px solid #fff;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.text {
  font-size: 23.5px;
  font-weight: 300;
  color: #fff;
}
