@import "reset";
@import "mixins";

*,
*::before,
*::after {
  box-sizing: border-box;
}

*::selection {
  // color: $c-white;
  // background: $c-red;
}

html {
  // font-family: $f-general;
  font-weight: 400;
  font-size: 14px;
  // color: $c-black;
  scroll-behavior: smooth;
}

body {
  // background-color: $c-background;
  overflow-x: hidden;
}

.container {
  margin-left: auto;
  margin-right: auto;
  max-width: 998px;
  // @include desktop {
  //   max-width: 1200px;
  // }

  // @include desktopWX {
  //   max-width: 90%;
  // }

  @include iPadMini {
    max-width: 100%;
    padding-right: 20px;
    padding-left: 20px;
  }

  &--small {
    max-width: 1270px;
    margin: 0 auto;

    @include desktop {
      max-width: 1200px;
    }

    @include desktopWX {
      max-width: 90%;
    }

    @include iPadMini {
      max-width: 100%;
      padding-right: 20px;
      padding-left: 20px;
    }
  }
}

// h1,
// h2 {
//   @include title;
// }

// a {
//   color: $c-gray-dark;
// }

@media (max-width: 375px) {
  .container {
    width: 100%;

    @include desktop {
      max-width: 1200px;
    }

    @include desktopWX {
      max-width: 90%;
    }

    @include iPadMini {
      max-width: 100%;
      padding-right: 20px;
      padding-left: 20px;
    }

    &--small {
      max-width: 1270px;
      margin: 0 auto;

      @include desktop {
        max-width: 1200px;
      }

      @include desktopWX {
        max-width: 90%;
      }

      @include iPadMini {
        max-width: 100%;
        padding-right: 20px;
        padding-left: 20px;
      }
    }
  }
}
