@import "src/styles/index";

.section {
  padding: 29px 0 19px 0;
}

.title {
  text-align: center;
  font-size: 31.5px;
  font-weight: 300;
  color: #39373a;
}

.groupBlocks {
  margin-top: 29px;
  display: flex;
  column-gap: 36px;
  row-gap: 25px;
  @include mobile {
    column-gap: 15px;
    flex-wrap: wrap;
  }
}

.block {
  cursor: pointer;
  text-align: center;
  width: calc(25% - 18px);
  @include mobile {
    width: calc(50% - 7.5px);
  }
}
.wrapperIcon {
  height: 249px;
  width: 100%;
  img {
    width: 100%;
    height: 100%;
  }
}
.text {
  display: block;
  padding: 18px 0 14px 0;

  font-size: 20.5px;
  font-weight: 300;
  text-align: center;
  color: #fff;
  background-color: #5c5a5d;
}
