@import "src/styles/index";

.section {
  background-color: #39373a;
  @include mobile {
    padding-left: 15px;
    padding-right: 15px;
  }
}
.inner {
  display: flex;
  justify-content: space-between;
  padding-top: 36px;
  padding-bottom: 27px;
  @include mobile {
    flex-direction: column;
    row-gap: 20px;
  }
}

.name {
  font-size: 37px;
  color: white;
}

.logo {
}

.wrapperTell {
  display: flex;
  align-items: center;
  column-gap: 30px;
  @include iPadMini {
    flex-direction: column;
    row-gap: 10px;
  }
}
.tell {
  display: block;
  font-family: Calibri;
  font-size: 22.5px;
  font-weight: 300;
  color: #fff;
}
.button {
  background-color: #5c5a5d;
  font-size: 18px;
  font-weight: 300;
  color: #fff;
  padding: 8px 21px 8px 22px;
  border-radius: 13.2px;
  cursor: pointer;
  &:hover {
    color: rgb(81, 201, 161);
  }
}
